import mpvueEcharts from '../../../components/mpvue-echarts/src/echarts.vue';

export default {
	data(){
		return{
			echarts,
			type:0,
			type1:0,
			sall:'',
			num:30,
			toast:false,
			sallNum:'',//卖出个数
			haveNum:6666,//剩余个数
			earnings:'0.00',//卖出收益
			unitPrice:'',//果实单价
			lineChart:{}
		}
	},
	onLoad(options) {
		uni.setNavigationBarColor({
			frontColor: '#ffffff',
			backgroundColor: '#2a99de',
			animation: {
				duration: 400,
				timingFunc: 'easeIn'
			}
		})
	},
	methods:{
		// 折线图初始化函数
		lineInit: function (canvas, width, height) {
			let lineChart = this.echarts.init(canvas, null, {
				width: width,
				height: height
			})
			canvas.setChart(lineChart);
			lineChart.setOption(this.getLineOption());
			// this.lineChart=lineChart
			// return lineChart
		},
		// 折线图基本配置项
		getLineOption() {
			let option={
				animation: false,
				color: ['#ffffff'],
				animation: true,
				tooltip : {
					trigger: 'axis'
				},
				legend: {
					data:['','']
				},
				toolbox: {
					show : true,
					feature : {
						mark : {show: true},
						dataZoom : {show: true},
						magicType : {show: true, type: ['line', 'bar', 'stack', 'tiled']},
						restore : {show: true},
						saveAsImage : {show: true},
					}
				},
				calculable : true,
				xAxis : [
					{
						type : 'category',
						boundaryGap : false,
						axisLine: {
						lineStyle: {
							width: '1',
							color: "#ffffff", //坐标线颜色	
						}
					},
				splitLine: {
					show: true, //y轴线是否显示
					lineStyle: {
						width: '1', //线的宽度
						color: "#ffffff", //线的颜色
						type : 'dashed' 
					},
				},
				data : function (){
					var list = [];
					for (var i = 1; i <= 30; i++) {
						list.push('3-' + i);
					}
					return list;
					}()
				}
				],
				yAxis : [
					{
						type : 'value',
						axisLine: {
						lineStyle: {
							width: '1',
							color: "#ffffff", //坐标线颜色	
						}
					},
					position:'right',
					splitLine: {
						show: true, //y轴线是否显示
						lineStyle: {
						width: '1', //线的宽度
						color: "#ffffff", //线的颜色
						type : 'dashed' 
						},
					},
				}
				],
				series : [
					{
					name:'最高',
					type:'line',
					data:function (){
						var list = [];
						for (var i = 1; i <= 30; i++) {
							list.push(Math.round(Math.random()* 30));
							}
						return list;
						}()
					},
				]
			}
			return option;
		},
		choice(e){
			this.type = e.currentTarget.dataset.type;
			console.log(this.type)
		},
		choice1(e){
			this.type1 = e.currentTarget.dataset.type1;
			// this.lineChart.setOption(this.getLineOption());
			console.log(this.lineChart,this.type1)
		},
		// 显示弹窗
		toastShow(e){
			this.sall = e.currentTarget.dataset.sall;
			this.toast=true;
			console.log(this.sall)
		},
		// 关闭弹窗
		toastHide(){
			this.toast=false;
		},
		// 判断算量是否够
		judge(){
			
		},
		// 计算果实总价格
		count(){
			if(this.sallNum.trim()==''){
				uni.showToast({
					title: '请输入要卖出的数量!',
					duration:2000,
					icon:'none'
				});
				this.earnings='0.00'
				return;
			}else{
				this.earnings=this.sallNum*this.unitPrice;
			}
		},
		// 曲线图加载
	},
	components: {
		mpvueEcharts
	}
}



